@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@700&display=swap);
html,
body {
  height: 100vh;
}

#root,
.App {
  height: 100%;
}

body {
  margin: 0;
  font-family: "Fira Code", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p {
  margin: 0;
}

header {
  box-sizing: border-box;
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  background: lightgrey;
  font-size: 2rem;
  padding: 0.4rem 0.8rem;
}

footer {
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: lightgrey;
  padding: 0.8rem;
  text-align: center;
  font-size: 0.9rem;
}

.gh-icon-placeholder {
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
  background: white;
}

.game-container {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background: lightskyblue;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.game-active {
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100%;
  width: 100%;
  padding: 4rem 0;
}

.game-play-container {
  box-sizing: border-box;
  width: 100%;
}

.score-board {
  text-align: center;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 2rem;
}

.score-board__current-score {
  font-size: 4.5rem;
}

.word-selector {
  width: 100%;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.game-over {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  padding: 2rem;
  margin: 3rem 0;
}

.game-over__word-list {
  box-sizing: border-box;
  max-height: 40vh;
  overflow-y: scroll;
  border: 2px solid white;
  border-radius: 15px;
  padding: 0.3rem;
  padding-left: 3rem;
}

.btn {
  box-sizing: border-box;
  display: block;
  margin: 0.5rem;
  padding: 0.8rem 1rem;
  font-size: 1.5rem;
  font-family: "Fira Code";
  border: none;
  border-radius: 16px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

